.tur-wraper-input {
  padding: 0 0 15px 0;
  width: 100%;
}

.tur-wraper-input label {
  display: block;
  padding-bottom: 5px;
  visibility: hidden;
  position: absolute;
  top: -1;
}

.tur-wraper-input input[type="text"],
.tur-wraper-input input[type="email"],
.tur-wraper-input input[type="date"],
.tur-wraper-input input[type="number"] {
  width: 100%;
  padding: 10px 0;
  line-height: 19px;
  border: 0;
  border-bottom: 1px solid #dddddd;
  outline: none;
}

.tur-wraper-input input[type="text"]:focus,
.tur-wraper-input input[type="email"]:focus,
.tur-wraper-input input[type="date"]:focus,
.tur-wraper-input input[type="number"]:focus {
  border-color: #000000;
}